.dynamic-dashboard{
    display: flex;
    flex-direction: column;
    
    & > div+div{
        margin-top: 2.4rem;
    }
    
    & > div{
        flex: 1;
        order: 2;
    }
    
    & > .communication{
        flex: none;
        width: 100%;
        order: 1;
        margin-bottom: 2.4rem;
        margin-top: 0;
    }

    @media (min-width: $g-bp1-m) {
        flex-direction: row;
        & > div+div{
            margin-left: 2.4rem;
            margin-top: 0;
            margin-bottom: 0;
        }
        & > .communication{
            width: 45%;
            order: 3;
            margin-bottom: 0;
        }        
    }
    @media (min-width: $g-bp2) {
        flex-direction: column;
        & > div+div{
            margin-left: 0;
            margin-top: 2.4rem;
        }
        & > .communication{
            width: 100%;
            order: 1;
            margin-bottom: 2.4rem;
        }
    }
    @media (min-width: $g-bp3) {
        flex-direction: row;
        & > div+div{
            margin-left: 2.4rem;
            margin-top: 0;
            margin-bottom: 0;
        }
        & > .communication{
            width: 32%;
            order: 3;
            margin-bottom: 0;
        }
    }
    @media (min-width: $g-bp4) {
        & > .communication{
            width: 37%;
        }
    }
}